import dataKa from './DataKa'
import dataMk from './DataMk'
var stemmer = require('stemmer')

var natural = require('natural')
var tokenizer = new natural.WordTokenizer()

//sort Mk ID asc
dataMk.sort((a, b) => parseFloat(a.id) - parseFloat(b.id))
var kaToken = []
var mkToken = []

var ignoredToken = ['of', 'in', 'a', 'an', 'such', 'as', 'use', 'among', 'best', 'and', 'or', 'e', 'g',
                   'the', 'for', 'vs', 's', 'The', 'to', 'non', 'via', 're', 'by', 'is', 'i', 'also', 'IS', 'An',
                   'introduction', 'Introduction', 'just', 'another', 'that', 'this', 'using', 'with', 'including',
                   'their', 'etc'
                ]

// tokenize data KA
function dataKaTokenize() {
    for (var i = 0; i < dataKa.length; i++) {
        //build same structure for ka array
        var kaObj = {}
        kaObj['id'] = dataKa[i].id
        kaObj['area_code'] = dataKa[i].area_code
        kaObj['knowledge_area'] = dataKa[i].knowledge_area
        kaObj['chapters'] = []
        // chapter
        for (var j = 0; j < dataKa[i].chapters.length; j++) {
            var kaChapterObj = {}
            kaChapterObj['chapter'] = dataKa[i].chapters[j].chapter
            
            var combinedTopics = []
            //topics
            for (var k = 0; k < dataKa[i].chapters[j].topics.length; k++) {
                var tokenized = tokenizer.tokenize(dataKa[i].chapters[j].topics[k]);

                for (var l = 0; l < tokenized.length; l++) {
                    //prevent same word to be added twice
                    var isDuplicated = false
                    var stemmedWord =  stemmer(tokenized[l].toLowerCase())
                    
                    for (var m = 0; m < combinedTopics.length; m++) {
                        if(combinedTopics[m] == stemmedWord){
                            isDuplicated = true
                            break
                        }
                    }
                    if(!isDuplicated) {
                        //check if token are labelled as ignored Token
                        var isIgnored = false
                        for (var n = 0; n < ignoredToken.length; n++) {
                            if ( stemmedWord == ignoredToken[n] ){
                                isIgnored = true
                                break
                            }
                        }
                        if(!isIgnored){
                            combinedTopics.push(stemmedWord)
                        }
                    }
                } 
            }
            kaChapterObj['tokenized'] = combinedTopics
            kaObj['chapters'].push(kaChapterObj)
        }
        kaToken.push(kaObj)
    }
}


//tokenize data MK
function dataMkTokenize() {
    for (let i = 0; i < dataMk.length; i++) {
        //build same structure for ka array
        var mkObj = {}
        mkObj['id'] = dataMk[i].id
        mkObj['mkName'] = dataMk[i].mkName
        mkObj['sks'] = dataMk[i].sks
        mkObj['topics'] = dataMk[i].topics
        mkObj['tokenized'] = []
        // chapter
        var combinedTopics = []
        for (let j = 0; j < dataMk[i].topics.length; j++) {
            //topics
            var tokenized = tokenizer.tokenize(dataMk[i].topics[j]);

            for (let k = 0; k < tokenized.length; k++) {
                //prevent same word to be added twice
                var isDuplicated = false
                var stemmedWord =  stemmer(tokenized[k].toLowerCase())
                for (let m = 0; m < combinedTopics.length; m++) {
                    if(combinedTopics[m] == stemmedWord){
                        isDuplicated = true
                        break
                    }
                }
                if(isDuplicated == false) {
                    var isIgnored = false
                    for (var m = 0; m < ignoredToken.length; m++) {
                        if ( stemmedWord == ignoredToken[m] ){
                            isIgnored = true
                            break
                        }
                    }
                    if(!isIgnored){
                        combinedTopics.push(stemmedWord)
                    }
                }
            } 
        }
        mkObj['tokenized'] = combinedTopics
        mkToken.push(mkObj)
    }
}
dataKaTokenize()
dataMkTokenize()
export function preProcessStemmed() {
    return { kaToken, mkToken }
}