import dataKa from './DataKa'
import dataMk from './DataMk'
//var stemmer = require('stemmer')

var natural = require('natural')
var tokenizer = new natural.WordTokenizer()

//sort Mk ID asc
dataMk.sort((a, b) => parseFloat(a.id) - parseFloat(b.id))
var kaToken = []
var mkToken = []

var ignoredToken = ['of', 'in', 'a', 'an', 'such', 'as', 'use', 'among', 'best', 'and', 'or', 'e', 'g',
                   'the', 'for', 'vs', 's', 'The', 'to', 'non', 'via', 're', 'by', 'is', 'i', 'also', 'IS', 'An',
                   'introduction', 'Introduction', 'just', 'another', 'that', 'this', 'using', 'with', 'including',
                   'their', 'etc', 'concept', '3', 'tools', 'concepts', 'oriented', 'from', 'method', 'methods',
                   '1', '2', '3', '4', '5', '6', '7', '9', '10', 'program', 'data', '0', 'program', '', 'computer', 'differences', 'difference', 'free', 'means', 'its', 'it', 'so', 'on', 'see', 'why', 'still', 'l', 'how', 'other', 'not', 'found', 'correct', 'based', 'topics', 'same', 'into', 'the', 'preliminary', 'student', 'assignment', 'definition', 'definitions', 'overview', 'expected', 'no', 'can', 'be', 'have', 'like', 'only', 'are', 'at', 'caused', 'some', 'given', 'take', 'more', 'each', 'them', 'defined', 'creating', 'cannot', 'within', 'they', 'we'
                ]

// tokenize data KA
function dataKaTokenize() {
    for (var i = 0; i < dataKa.length; i++) {
        // KA array structure
        var kaObj = {}
        kaObj['id'] = dataKa[i].id
        kaObj['area_code'] = dataKa[i].area_code
        kaObj['knowledge_area'] = dataKa[i].knowledge_area
        kaObj['chapters'] = []
        // [revision] ignore chapter
        var kaTokenized = []
        // loop chapter
        for (var j = 0; j < dataKa[i].chapters.length; j++) {
            var kaChapterObj = {}
            kaChapterObj['chapter'] = dataKa[i].chapters[j].chapter
            var combinedTopics = []
            // loop topics
            for (var k = 0; k < dataKa[i].chapters[j].topics.length; k++) {
                var tokenized = tokenizer.tokenize(dataKa[i].chapters[j].topics[k]);
                for (var l = 0; l < tokenized.length; l++) {
                    // case folding
                    if(kaTokenized.length == 0){
                        kaTokenized.push(tokenized[l].toLowerCase())
                    }
                    else {
                        // check if terms already exist / added
                        var isDuplicated = false
                        for (var m = 0; m < kaTokenized.length; m++) {
                            if(kaTokenized[m] == tokenized[l].toLowerCase()){
                                isDuplicated = true
                                break
                            }
                        }
                        if(isDuplicated == false) {
                            // stopwords (ignore term included in stopword)
                            var isIgnored = false
                            for (var n = 0; n < ignoredToken.length; n++) {
                                if ( tokenized[l].toLowerCase() == ignoredToken[n] ){
                                    isIgnored = true
                                    break
                                }
                            }
                            if(!isIgnored){
                                combinedTopics.push(tokenized[l].toLowerCase())
                                kaTokenized.push(tokenized[l].toLowerCase())
                            }
                        }
                    }
                    
                } 
            }
            kaChapterObj['tokenized'] = kaTokenized
            kaObj['chapters'].push(kaChapterObj)
        }
        kaObj['tokenized'] = kaTokenized
        kaToken.push(kaObj)
    }
}


//tokenize data MK
function dataMkTokenize() {
    for (let i = 0; i < dataMk.length; i++) {
        //build same structure for ka array
        var mkObj = {}
        mkObj['id'] = dataMk[i].id
        mkObj['mkName'] = dataMk[i].mkName
        mkObj['sks'] = dataMk[i].sks
        mkObj['topics'] = dataMk[i].topics
        mkObj['tokenized'] = []
        // chapter
        
        var combinedTopics = []
        for (let j = 0; j < dataMk[i].topics.length; j++) {
            //topics
            var tokenized = tokenizer.tokenize(dataMk[i].topics[j]);
            for (let k = 0; k < tokenized.length; k++) {
                //prevent same word to be added twice
                if(combinedTopics.length == 0){
                    combinedTopics.push(tokenized[k].toLowerCase())
                }
                else {
                    var isDuplicated = false
                    for (let m = 0; m < combinedTopics.length; m++) {
                        if(combinedTopics[m] == tokenized[k].toLowerCase()){
                            isDuplicated = true
                            break
                        }
                    }
                    if(isDuplicated == false) {
                        var isIgnored = false
                        for (var m = 0; m < ignoredToken.length; m++) {
                            if ( tokenized[k] == ignoredToken[m] ){
                                isIgnored = true
                                break
                            }
                        }
                        if(isIgnored == false){
                            combinedTopics.push(tokenized[k].toLowerCase())
                        }
                    }
                }
                
            } 
        }
        mkObj['tokenized'] = combinedTopics
        mkToken.push(mkObj)
    }
}
dataKaTokenize()
dataMkTokenize()
export function preProcess() {
    return { kaToken, mkToken }
}