<template>
  <div class="home">
    <Header page="singleMk"></Header>
    <!-- Main container -->
    <main class="main-container">

      <div class="main-content">

        <div class="row">
          <div class="col-12">
            <div class="card">
              <div class="card-body text-left">
                <h3>Show Detailed Single Informatics Engineering Subject</h3>
                <hr>
                <div class="row">
                  <div class="col-md-6">
                    <select name="" id="" v-model="inputMk" data-provide="selectpicker" data-live-search="true" class="form-control form-control-lg" data-width="70%">
                      <option value="0" default disabled>--- SELECT MK ---</option>
                      <option :value="mkProbKaItem.id" v-for="mkProbKaItem in mkProbKa" :key="mkProbKaItem.id" :data-subtext="mkProbKaItem.id">{{ mkProbKaItem.mkName.toUpperCase() }}</option>
                    </select>
                <button class="btn btn-md btn-dark ml-3" @click="analyzeMk()">GET DETAIL!</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        
        <div class="row">
      
          <div class="col-12" v-if="mkProbKaActive">
            <div class="card">
              <header class="card-header">
                <h3 class="card-title p-2 pb-0 mb-0"><strong>{{ singleMk.id }}</strong> - {{singleMk.mkName}}</h3>
              </header>

              <div class="card-body">
                <div class="row">
                  <div class="col-4"  v-for="(mkProbKaItem, Kindex) in singleMk.kaProb" :key="Kindex">
                    
                        <h4 class="card-title text-left card-shadowed mb-4">
                          <strong>{{ Kindex + 1 }} - {{ mkProbKaItem.area_code }}</strong> - {{ mkProbKaItem.knowledge_area }} 
                          <br> 
                          <hr class="m-2"> 
                          Kontribusi MK ke Area[<strong>{{ mkProbKaItem.area_code }}</strong>] = <strong>{{ mkProbKaItem.contribution }} </strong> <br> Probability = <strong>{{ mkProbKaItem.finalKaProb }}</strong>
                        </h4>
                    
                  </div>
                </div>
              </div>
            </div>
          </div>








        </div>
      </div><!--/.main-content -->


    </main>
    <!-- END Main container -->
  </div>
</template>

<script>
// @ is an alias to /src
import { matchingResultNb } from '../logic/MatchingTopicsNb'
import Header from '../components/Header'

export default {
  name: 'SingleMk',
  components: {
    Header
  },
  data () {
    return {
      mkProbKa: [],
      matchedKaMk: [],
      inputMk: 0,
      singleMk: {},
      mkProbKaActive: false
    }
  },
  created () {
    let matchingRes = matchingResultNb()
    this.mkProbKa = matchingRes.mkProbKa
    this.matchedKaMk = matchingRes.matchedKaMk
  },
  methods:{
    analyzeMk(){
      this.mkProbKaActive = true
      function search(id, myArray){
          for (var i=0; i < myArray.length; i++) {
              if (myArray[i].id === id) {
                  return myArray[i];
              }
          }
      }
      var resultObject = search(this.inputMk, this.mkProbKa);
      this.singleMk = resultObject
    },
  }
}
</script>

