import { matchingResult } from './MatchingTopics'
import { matchingResultNb } from './MatchingTopicsNb'
import dataKa from './DataKa'
import { match } from 'minimatch'

let MatchTop = matchingResult()
var matchedKaMk = MatchTop.matchedKaMk

let MatchTopNb = matchingResultNb()
var matchedKaMkNb = MatchTopNb.matchedKaMk
var matchedKaMkNbFull = MatchTopNb.mkProbKa

var distributedMk = []
var distributedMkNb = []
var distributedMkNbFull = []
function distributeMk(){
    for (let i = 0; i < dataKa.length; i++) {
        var kaObj = {}
        kaObj['id'] = dataKa[i].id
        kaObj['area_code'] = dataKa[i].area_code
        kaObj['knowledge_area'] = dataKa[i].knowledge_area
        kaObj['mk'] = []
        for (let j = 0; j < matchedKaMk.length; j++) {
            if(matchedKaMk[j].matchedKa.matchedKaId == dataKa[i].id) {
                var mkObj = {}
                mkObj['id'] = matchedKaMk[j].id
                mkObj['sks'] = matchedKaMk[j].sks
                mkObj['mkName'] = matchedKaMk[j].mkName
                kaObj['mk'].push(mkObj)
            }
        }

        distributedMk.push(kaObj)
    }
}
function distributeMkNbOld(){
    for (let i = 0; i < dataKa.length; i++) {
        var kaObj = {}
        kaObj['id'] = dataKa[i].id
        kaObj['area_code'] = dataKa[i].area_code
        kaObj['knowledge_area'] = dataKa[i].knowledge_area
        kaObj['mk'] = []
        for (let j = 0; j < matchedKaMkNb.length; j++) {
            if(matchedKaMkNb[j].matchedKa.matchedKaId == dataKa[i].id) {
                var mkObj = {}
                mkObj['id'] = matchedKaMkNb[j].id
                mkObj['sks'] = matchedKaMkNb[j].sks
                mkObj['mkName'] = matchedKaMkNb[j].mkName
                mkObj['contribution'] = matchedKaMk[j].contribution
                kaObj['mk'].push(mkObj)
            }
        }

        distributedMkNb.push(kaObj)
    }
}
function distributeMkNb(){
    for (let i = 0; i < dataKa.length; i++) {
        var kaObj = {}
        kaObj['id'] = dataKa[i].id
        kaObj['area_code'] = dataKa[i].area_code
        kaObj['knowledge_area'] = dataKa[i].knowledge_area
        kaObj['mk'] = []
        // check all MK 
        for (let j = 0; j < matchedKaMkNbFull.length; j++) {
            var matched = false
            // check if there is contribution to KA in current MK
            for (let k = 0; k < matchedKaMkNbFull[j].kaProb.length; k++) {
                if(matchedKaMkNbFull[j].kaProb[k].id == dataKa[i].id){
                    matched = true
                    var mkObj = {}
                    mkObj['id'] = matchedKaMkNbFull[j].id
                    mkObj['sks'] = matchedKaMkNbFull[j].sks
                    mkObj['mkName'] = matchedKaMkNbFull[j].mkName
                    mkObj['contribution'] = matchedKaMkNbFull[j].kaProb[k].contribution
                    kaObj['mk'].push(mkObj)
                    break
                }
            }
        }
        var sorted = kaObj['mk'].sort((a,b) => parseFloat(b.contribution) - parseFloat(a.contribution))
        kaObj['mk'] = sorted 
        distributedMkNbFull.push(kaObj)
    }
}
distributeMk()
distributeMkNbOld()
distributeMkNb()
export function mkDistribution(){
    return { distributedMk, distributedMkNb, distributedMkNbFull }
}
