import { preProcessStemmed } from './PreProcessStemmed'

let prePro = preProcessStemmed()

var kaToken = prePro.kaToken
var mkToken = prePro.mkToken


//generating matching percentage for each MK
var mkProbKa = []



function generatePercentage() {
    for (let i = 0; i < mkToken.length; i++) {
        //build same structure for ka array
        var mkObj = {}
        mkObj['id'] = mkToken[i].id
        mkObj['mkName'] = mkToken[i].mkName
        
        var sortedKa = []
       
        // Knowledge Area
        for (let j = 0; j < kaToken.length; j++) {
            var kaObj = {}
            kaObj['id'] = kaToken[j].id
            kaObj['area_code'] = kaToken[j].area_code
            kaObj['knowledge_area'] = kaToken[j].knowledge_area
            
            var kaMkProbs = []
            
            for (let m = 0; m < kaToken[j].chapters.length; m++) {
                var kaMkProbsObj = {}
                kaMkProbsObj['chapter'] = kaToken[j].chapters[m].chapter
                

                var totalMatchedWord = 0
                // get total matched word
                // loop MK token
                for (let n = 0; n < mkToken[i].tokenized.length; n++) {
                    // loop KA token
                    for (let p = 0; p < kaToken[j].chapters[m].tokenized.length; p++) {
                        
                        // check if token equals
                        if( mkToken[i].tokenized[n].toLowerCase() == kaToken[j].chapters[m].tokenized[p].toLowerCase()) {
                            totalMatchedWord++;
                        }                        
                    }
                } 
                var getFixedValue = ((totalMatchedWord + 1) / (mkToken[i].tokenized.length + 1) )
                kaMkProbsObj['mkProb'] = parseFloat(getFixedValue)
                kaMkProbs.push(kaMkProbsObj)
            }
            //sort kaMkProbs

            kaMkProbs.sort((a, b) => parseFloat(b.mkProb) - parseFloat(a.mkProb));
            kaObj['chapters'] = kaMkProbs
            //get Ka Prob using Naive Bayes 
            // mkxkaProb = all chapter prob * kaProb
            var mkxKaProb = 1/kaToken.length
            for (let m = 0; m < kaMkProbs.length; m++) {
                mkxKaProb = mkxKaProb * ( kaMkProbs[m].mkProb )
            }
            kaObj['finalKaProb'] = mkxKaProb
            sortedKa.push(kaObj)
        }

        sortedKa.sort((a, b) => parseFloat(b.finalKaProb) - parseFloat(a.finalKaProb))
        mkObj['kaProb'] = sortedKa
        mkProbKa.push(mkObj)
    }
}

var matchedKaMk = []
function matchingKaMk() {
    for (let i = 0; i < mkProbKa.length; i++) {
        //build same structure for ka array
        var kaObj = {}
        kaObj['id'] = mkProbKa[i].id
        kaObj['mkName'] = mkProbKa[i].mkName
        kaObj['sks'] = mkProbKa[i].sks
        kaObj['matchedKa'] = {}
        var matchedKaId = 0
        var matchedKaCode = 'x'
        var matchedKaName = 'x'
        var matchedKaProb = 0
        //Ka Chapter
        for (let j = 0; j < mkProbKa[i].kaProb.length; j++) {
            //get one MK which has highest matching percentage
                //initial value
            if(j == 0) {
                matchedKaId = mkProbKa[i].kaProb[j].id
                matchedKaCode = mkProbKa[i].kaProb[j].area_code
                matchedKaName = mkProbKa[i].kaProb[j].knowledge_area
                matchedKaProb = mkProbKa[i].kaProb[j].finalKaProb
            }
            //update if there is higher percentage
            else {
                //check if there is higher percentage
                if(matchedKaProb < mkProbKa[i].kaProb[j].finalKaProb ){
                    matchedKaId = mkProbKa[i].kaProb[j].id
                    matchedKaCode = mkProbKa[i].kaProb[j].area_code
                    matchedKaName = mkProbKa[i].kaProb[j].knowledge_area
                    matchedKaProb = mkProbKa[i].kaProb[j].finalKaProb
                }
            }
        }
        kaObj['matchedKa']['matchedKaId'] = matchedKaId
        kaObj['matchedKa']['matchedKaCode'] = matchedKaCode
        kaObj['matchedKa']['matchedKaName'] = matchedKaName
        kaObj['matchedKa']['matchedKaProb'] = matchedKaProb
        matchedKaMk.push(kaObj)
    }
}

generatePercentage()
matchingKaMk()
export function matchingResultStemmed(){
    return { mkProbKa , matchedKaMk }
}