<template>
  <div class="home">
    <Header page="compareToken"></Header>
    <!-- Main container -->
    <main class="main-container">

      <div class="main-content">

        <div class="row">
          <div class="col-12">
            <div class="card">
              <div class="card-body text-left">
                <h3>Compare Token</h3>
                <hr>
                <div class="row">
                  <div class="col-md-5">
                    <select name="" id="" v-model="inputMk" data-provide="selectpicker" data-live-search="true" class="form-control form-control-lg" data-width="100%">
                      <option value="0" default disabled>--- SELECT MATA KULIAH ---</option>
                      <option :value="mkProbKaItem.id" v-for="mkProbKaItem in mkProbKa" :key="mkProbKaItem.id" :data-subtext="mkProbKaItem.id">{{ mkProbKaItem.mkName.toUpperCase() }}</option>
                    </select>
                  </div>
                  <div class="col-md-6">
                    <select name="" id="" v-model="inputKa" data-provide="selectpicker" data-live-search="true" class="form-control form-control-lg" data-width="70%">
                      <option value="0" default disabled>--- SELECT KNOWLEDGE AREA ---</option>
                      <option :value="kaTokenItem.id" v-for="kaTokenItem in kaToken" :key="kaTokenItem.id" :data-subtext="kaTokenItem.area_code">{{ kaTokenItem.knowledge_area.toUpperCase() }}</option>
                    </select>
                    <button class="btn btn-md btn-dark ml-3" @click="analyzeMk()">GET DETAIL!</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        
        <div class="row">
      
          <div class="col-12" v-if="mkProbKaActive">
            <div class="card">
              <header class="card-header">
                <h3 class="card-title p-2 pb-0 mb-0"><strong>Comparing</strong> - Token</h3>
              </header>

              <div class="card-body">
                <div class="row text-left" v-for="singleKaProbItemz in singleKaProb[0].kaProb" :key="singleKaProbItemz">
                  <div class="col-3">
                    <h4 v-if="singleKaProbItemz.id == inputKa">Total Token MK = {{ singleKaProb[0].tokenLength }}</h4>
                  </div>
                  <div class="col-3">
                    <h4 v-if="singleKaProbItemz.id == inputKa">Total Token KA = {{ singleKaProbItemz.tokenLength }}</h4>
                  </div>
                  <div class="col-2">
                    <h4 v-if="singleKaProbItemz.id == inputKa">Matched = <strong> {{ singleKaProbItemz.matchedWord }}</strong></h4>
                  </div>
                </div>
                <div class="row">
                  <div class="col-3">
                    <h5 class="card-title p-2 pb-0 mb-0"><strong>{{ singleMk[0].id }}</strong> - {{singleMk[0].mkName}}</h5>
                    <table class="table table-sm">
                      <thead>
                        <tr class="text-left">
                          <th class="f-bold" style="width:100px;">#</th>
                          <th class="f-bold" style="width:250px;" >Tokenize</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr v-for="(mkTokenItem, index) in singleMk[0].tokenized" :key="index" class="text-left">
                          <th scope="row" >{{ index + 1 }}</th>
                          <td>{{ mkTokenItem }}</td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                  <div class="col-9" >
                    <div class="">
                      <h5 class="card-title p-2 pb-0 mb-0"><strong>{{ singleKa[0].area_code }}</strong> - {{singleKa[0].knowledge_area}}</h5>
                      <div class="d-flex" style="flex-wrap: wrap;">
                       <div class="text-left py-2 px-1 bb-1 border-info h-41px" style="width: 175px;"   v-for="(kaTokenItem, index) in singleKa[0].tokenized" :key="index" >
                          <strong class="text-danger fw-600" v-if="highlightWord(kaTokenItem)">** {{ kaTokenItem }} **</strong>
                          <span v-else>{{ kaTokenItem }}</span>
                        </div>
                      </div> 
                    </div>
                    
                  </div>
                </div>
              </div>
            </div>
          </div>








        </div>
      </div><!--/.main-content -->


    </main>
    <!-- END Main container -->
  </div>
</template>

<script>
// @ is an alias to /src
import { matchingResult } from '../logic/MatchingTopics'
//import { matchingResultStemmed } from '../logic/MatchingTopicsStemmed'
import { preProcess } from '../logic/PreProcess'
//import { preProcessStemmed } from '../logic/PreProcessStemmed'
import Header from '../components/Header'

export default {
  name: 'SingleMk',
  components: {
    Header
  },
  data () {
    return {
      tokenIndex: 0,
      kaToken: [],
      mkToken: [],
      mkProbKa: [],
      inputMk: 0,
      inputKa: 0,
      singleMk: {},
      singleKa: {},
      singleKaProb: {},
      singleKaProbChapter: {},
      singleKaProbItem: {},
      mkProbKaActive: false
    }
  },
  created () {
    //let matchingRes = matchingResult()
    let matchingRes = matchingResult()
    this.mkProbKa = matchingRes.mkProbKa
    let prePro = preProcess()
    this.kaToken = prePro.kaToken
    this.mkToken = prePro.mkToken
    
  },
  methods:{
    analyzeMk(){
      this.mkProbKaActive = true
      this.singleMk = this.mkToken.filter(mkToken => mkToken.id == this.inputMk)
      this.singleKa = this.kaToken.filter(kaToken => kaToken.id == this.inputKa)
      this.singleKaProb = this.mkProbKa.filter(mkId => mkId.id == this.inputMk)
      this.singleKaProbChapter = this.singleKaProb[0].kaProb;
      this.singleKaProbItem = this.singleKaProbChapter.filter(kaId => kaId.id == this.inputKa);
    },

    highlightWord(word){
      //prevent same word to be added twice
      var isDuplicated = false
      for (let m = 0; m < this.singleMk[0].tokenized.length; m++) {
          if(this.singleMk[0].tokenized[m] == word){
              isDuplicated = true
              break
          }
      }
      if(isDuplicated == true) {
        return true
      } else {
        return false
      }
    }
  }
}
</script>

