<template>
  <div class="home">
    <Header page="CalculateScore"></Header>
    <!-- Main container -->
    <main class="main-container">

      <div class="main-content">

        <div class="row">
          <div class="col-12">
            <div class="card">
              <div class="card-body text-left">
                <h3>Calculate Score </h3>
                <button class="btn btn-primary" @click="calculate">Calculate</button>
                <hr>
                <div class="card">
                  <div class="card-body">
                    <div class="row" v-if="resultShow">
                      <div class="col-12"  v-for="(scoreMkId, Kindex) in scoreMkDistribution" :key="Kindex">

                          <div class="card  card-shadowed mb-4  text-left  card-slided-up">
                            <header class="card-header">
                              <h3 class="card-title">
                                <strong>{{ Kindex + 1 }} - {{ scoreMkId.area_code }}</strong> - {{ scoreMkId.knowledge_area }} 
                              </h3>
                              <ul class="card-controls">
                                <li><a class="card-btn-slide rotate-180" href="#"></a></li>
                              </ul>
                            </header>
                            <div class="card-content">
                              <div class="card-body ">
                                <div class="row fs-16">
                                  <div class="col-3">
                                    Total Mata Kuliah
                                  </div>
                                  <div class="col-9 fs-18">
                                    <p class=" font-weight-bold">{{scoreMkId.mk.length}}</p>
                                  </div>
                                </div>
                                <div class="row fs-16">
                                  <div class="col-3">
                                    Total SKS
                                  </div>
                                  <div class="col-9 fs-18">
                                    <p class=" font-weight-bold">{{scoreMkId.totalSks}}</p>
                                  </div>
                                </div>
                                <div class="row mb-3 fs-16">
                                  <div class="col-3">
                                    Sigma (Nilai * sks) * Kontribusi
                                  </div>
                                  <div class="col-9">
                                    <div class="row">
                                      <div class="col-3 bb-2 bt-1 py-1" v-for="(mk, mindex) in scoreMkId.mk" :key="mindex">
                                          <div class="" v-if="mindex == 0">
                                            ({{mk.scoreDis}}*{{mk.sks}})*{{mk.contribution * 100}}%  
                                          </div>
                                          <!-- <div class=" " v-else>
                                            <strong style="font-weight: 600;">+</strong> ({{mk.scoreDis}}*{{mk.sks}})*{{mk.contribution * 100}}%
                                          </div> -->
                                          <div class="" v-else-if="mindex">
                                            <strong style="font-weight: 600;"></strong> ({{mk.scoreDis}}*{{mk.sks}})*{{mk.contribution * 100}}%
                                          </div>
                                      </div>
                                    </div>
                                  </div>
                                </div>
                                <div class="row mb-3 fs-16">
                                  <div class="col-3">
                                    Hasil Sigma Nilai diatas
                                  </div>
                                  <div class="col-9  fs-18">
                                    <p class=" font-weight-bold">{{scoreMkId.totalScore}}</p>
                                  </div>
                                </div>
                                <!-- <div class="row my-0 fs-16">
                                  <div class="col-3">
                                    Sigma SKS*Kontribusi
                                  </div>
                                  <div class="col-9">
                                    <div class="d-inline" v-for="(mk, mindex) in scoreMkId.mk" :key="mindex">({{mk.sks}}*{{mk.contribution}}) <span class="font-weight-bold">+ </span></div> 
                                    <div class="d-inline" v-for="(mk, mindex) in scoreMkId.mk" :key="mindex">
                                      <span v-if="mindex">({{mk.sks}}*{{mk.contribution}}) <span class="font-weight-bold">+ </span></span></div>
                                  </div>
                                </div> -->
                                
                                <div class="row my-0 fs-16">
                                  <div class="col-3">
                                    1/Sigma SKS <span class="font-weight-bold">X</span> total Sigma Score
                                  </div>
                                  <div class="col-9 fs-18">
                                    <p class="font-weight-bold mb-0"> {{ 1 / scoreMkId.totalSks}} <span class="font-weight-bold">*</span> {{scoreMkId.totalScore}} </p>
                                  </div>
                                </div>
                                <hr class="p-0">
                                <div class="row my-0">
                                  <div class="col-3">
                                   <h4><strong> Total Performance </strong></h4>
                                  </div>
                                  <div class="col-9">
                                    <h4><span class="font-weight-bold"> {{scoreMkId.totalPerformance}}</span></h4>
                                  </div>
                                </div>
                              </div>
                            </div>
                          </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        
      </div><!--/.main-content -->


    </main>
    <!-- END Main container -->
  </div>
</template>

<script>
// @ is an alias to /src
import Header from '../components/Header'
import { mkDistribution } from '../logic/MkDistribution'
import dataNilai from '../logic/DataNilai'
import axios from 'axios'

export default {
  name: 'CalculateScore',
  components: {
    Header
  },
  data () {
    return {
      scoreMkDistribution: null,
      dataNilaix: null,
      dataNilaiUpdated: null,
      resultShow: false
    }
  },
  created () {
    axios.get('https://reeyn.com/skripsi/data.php')
    .then(response => {
        this.dataNilaiUpdated = response.data
        this.dataNilaix = dataNilai 
        // assign score from excel to array
        for (let i = 0; i < dataNilai.length; i++) {
            for (let j = 0; j < this.dataNilaiUpdated.length; j++) {
              if (this.dataNilaix[i].id == this.dataNilaiUpdated[j].id) {
                  this.dataNilaix[i]['scoreDis'] = this.dataNilaiUpdated[j].scoreDis
              }   
            }
        } 
    })
    
  },
  mounted () {
    axios.get('https://reeyn.com/skripsi/data.php')
    .then(response => {
        this.dataNilaiUpdated = response.data
    })

  },
  methods:{
    calculate(){
      let matchingRes = mkDistribution()
      var sMkDist = matchingRes.distributedMkNbFull
      // preprocess convert discrete score to decimal score
      // assign score to Array
      for (let i = 0; i < sMkDist.length; i++) {
          // find score for each MK
          for (let j = 0; j < sMkDist[i].mk.length; j++) {
              for (let k = 0; k < this.dataNilaix.length; k++) {
                  if(sMkDist[i].mk[j].id == this.dataNilaix[k].id){
                      // assign discrete score
                      sMkDist[i].mk[j]['scoreDis'] = this.dataNilaix[k].scoreDis
                      if(this.dataNilaix[k].scoreDis == 'A'){
                          sMkDist[i].mk[j]['score'] = 4
                      }
                      else if(this.dataNilaix[k].scoreDis == 'B+'){
                          sMkDist[i].mk[j]['score'] = 3.5
                      }
                      else if(this.dataNilaix[k].scoreDis == 'B'){
                          sMkDist[i].mk[j]['score'] = 3
                      }
                      else if(this.dataNilaix[k].scoreDis == 'C+'){
                          sMkDist[i].mk[j]['score'] = 2.5
                      }
                      else if(this.dataNilaix[k].scoreDis == 'C'){
                          sMkDist[i].mk[j]['score'] = 2
                      }
                      else if(this.dataNilaix[k].scoreDis == 'D'){
                          sMkDist[i].mk[j]['score'] = 0
                      } 
                      else {
                          sMkDist[i].mk[j]['score'] = 0
                      }
                  }
              }
          }
      }

      // calculate sigma SKS for each KA and total Score for each KA
      for (let i = 0; i < sMkDist.length; i++) {
          // store total sks and score
          var totalSks = 0
          var totalScore = 0
          // tes jika cuma 10
          //for (let j = 0; j < 10; j++) {
          for (let j = 0; j < sMkDist[i].mk.length; j++) {
              // sum sks and score
              totalSks += sMkDist[i].mk[j].sks 
              totalScore += sMkDist[i].mk[j].score * sMkDist[i].mk[j].sks * sMkDist[i].mk[j].contribution 
          }
          sMkDist[i]['totalSks'] = totalSks
          sMkDist[i]['totalScore'] = totalScore
          sMkDist[i]['totalPerformance'] = totalScore / totalSks
      }

      var sorted = sMkDist.sort((a,b) => parseFloat(b.totalPerformance) - parseFloat(a.totalPerformance))
      sMkDist = sorted 
      this.scoreMkDistribution = sMkDist

      this.resultShow = true
    }
  }
}
</script>
