import { preProcess } from './PreProcess'
import { totalmem } from 'os'

let prePro = preProcess()

var kaToken = prePro.kaToken
var mkToken = prePro.mkToken


//generating matching percentage for each MK
var mkProbKa = []



function generatePercentage() {
    for (let i = 0; i < mkToken.length; i++) {
        //build same structure for ka array
        var mkObj = {}
        mkObj['id'] = mkToken[i].id
        mkObj['mkName'] = mkToken[i].mkName
        mkObj['sks'] = mkToken[i].sks
        
        var sortedKa = []
       
        // Knowledge Area
        for (let j = 0; j < kaToken.length; j++) {
            var kaObj = {}
            kaObj['id'] = kaToken[j].id
            kaObj['area_code'] = kaToken[j].area_code
            kaObj['knowledge_area'] = kaToken[j].knowledge_area
            
            var totalMatchedWord = 0
            // get total matched word
            
            // loop MK token
            for (let n = 0; n < mkToken[i].tokenized.length; n++) {
                // loop KA token
                for (let p = 0; p < kaToken[j].tokenized.length; p++) {
                    
                    // check if token equals
                    if( mkToken[i].tokenized[n].toLowerCase() == kaToken[j].tokenized[p].toLowerCase()) {
                        totalMatchedWord++;
                    }                        
                }
            } 

            var totalTokenMk = mkToken[i].tokenized.length 
            var totalTokenKa = kaToken[j].tokenized.length
            var getFixedValue = 0
            if (totalMatchedWord != 0){

                var allToken = ( totalTokenMk + totalTokenKa ) - totalMatchedWord
                // p(Mk) total sama / total token MK
                var pMk = (totalMatchedWord) / totalTokenMk
                //get p(ka|mk) * p(ka)
                //penyebut
                var numerator = ( ((totalMatchedWord) / allToken) / pMk ) * pMk
    
                // per
                getFixedValue = numerator / pMk
            }
            //sort kaMkProbs

            //get Ka Prob using Naive Bayes 
            var mkxKaProb = 1/kaToken.length * getFixedValue
            kaObj['finalKaProb'] = mkxKaProb
            kaObj['totalMatchedWord'] = totalMatchedWord
            sortedKa.push(kaObj)
        }

        sortedKa.sort((a, b) => parseFloat(b.finalKaProb) - parseFloat(a.finalKaProb))
        mkObj['kaProb'] = sortedKa
        mkProbKa.push(mkObj)
    }
}

function generatePercentageNb() {
    for (let i = 0; i < mkToken.length; i++) {
        if (mkToken[i].id != null){
        // buat struktur sama buat mk
        var mkObj = {}
        mkObj['id'] = mkToken[i].id
        mkObj['mkName'] = mkToken[i].mkName
        mkObj['sks'] = mkToken[i].sks
        var sortedKa = []
        var termCalculate = []
        // Naive bayes - loop MK token 
        for (let n = 0; n < mkToken[i].tokenized.length; n++) {
            // create object for termCalculate
            var termCalObj = {}
            termCalObj['token'] = mkToken[i].tokenized[n]
            termCalObj['kaProb'] = []
            var currentToken = mkToken[i].tokenized[n]
            var tokenAppear = 0
            // count token appear in all KA
            for (let p = 0; p < kaToken.length; p++) {
                for (let q = 0; q < kaToken[p].tokenized.length; q++) {
                    // check if token equals
                    if( currentToken == kaToken[p].tokenized[q].toLowerCase()) {
                        tokenAppear++
                    }                        
                }
            }
            // assign prob for each KA for each Term
            for (let p = 0; p < kaToken.length; p++) {
                termCalObj['kaId'] = kaToken[p].id
                var matched = false
                // check all KA token
                for (let q = 0; q < kaToken[p].tokenized.length; q++) {
                    // check if token equals
                    if( currentToken == kaToken[p].tokenized[q].toLowerCase()) {
                        matched = true
                        break
                    }                        
                }
                if (matched){
                    //laplace correction
                    var termMatchProb = 2 / (tokenAppear + kaToken.length)
                    termCalObj['kaProb'].push(termMatchProb)
                }
                else {
                    var termMatchProb = 1 / (tokenAppear + kaToken.length)
                    termCalObj['kaProb'].push(termMatchProb)
                }
            }
            termCalculate.push(termCalObj)
        } 
       
        // Knowledge Area
        for (let j = 0; j < kaToken.length; j++) {
            var kaObj = {}
            kaObj['id'] = kaToken[j].id
            kaObj['area_code'] = kaToken[j].area_code
            kaObj['knowledge_area'] = kaToken[j].knowledge_area
            kaObj['termProb'] = []
            var totalMatchedWords = 0
            // count matche word for current KA
            for (let k = 0; k < kaToken[j].tokenized.length; k++) {
                for (let m = 0; m < mkToken[i].tokenized.length; m++) {
                    if(kaToken[j].tokenized[k] == mkToken[i].tokenized[m]){
                        totalMatchedWords++
                    }
                }
            }
            kaObj['totalMatchedWords'] = totalMatchedWords
            // minimum matched token = 1
            if(totalMatchedWords > 0) {
                var numerator = 1/kaToken.length
                // assign term and prob from previous method
                for (let k = 0; k < termCalculate.length; k++) {
                    var termObj = {}
                    termObj['term'] = mkToken[i].tokenized[k]
                    termObj['prob'] = termCalculate[k].kaProb[j]
                    kaObj['termProb'].push(termObj)
                    numerator = numerator * termCalculate[k].kaProb[j]
                }
                kaObj['finalKaProb'] = numerator
                sortedKa.push(kaObj)
            }
        }
        // sort prob paling besar
        sortedKa.sort((a, b) => parseFloat(b.finalKaProb) - parseFloat(a.finalKaProb))
        mkObj['kaProb'] = sortedKa
        mkProbKa.push(mkObj)
        }
    }

    // assign MK contributions into each KA
    for (let i = 0; i < mkProbKa.length; i++) {
        var divider = 1
        for (let j = 0; j < mkProbKa[i].kaProb.length; j++) {
            divider = divider * 2
            mkProbKa[i].kaProb[j]['contribution'] = 1 / divider
        }
    }
}




var matchedKaMk = []
function matchingKaMk() {
    for (let i = 0; i < mkProbKa.length; i++) {
        //build same structure for ka array
        var kaObj = {}
        kaObj['id'] = mkProbKa[i].id
        kaObj['mkName'] = mkProbKa[i].mkName
        kaObj['sks'] = mkProbKa[i].sks
        kaObj['matchedKa'] = {}
        var matchedKaId = 0
        var matchedKaCode = 'x'
        var matchedKaName = 'x'
        var matchedKaProb = 0
        //Ka Chapter
        for (let j = 0; j < mkProbKa[i].kaProb.length; j++) {
            //get one MK which has highest matching percentage
            //initial value
            if(j == 0) {
                matchedKaId = mkProbKa[i].kaProb[j].id
                matchedKaCode = mkProbKa[i].kaProb[j].area_code
                matchedKaName = mkProbKa[i].kaProb[j].knowledge_area
                matchedKaProb = mkProbKa[i].kaProb[j].finalKaProb
            }
            //update if there is higher percentage
            else {
                //check if there is higher percentage
                if(matchedKaProb < mkProbKa[i].kaProb[j].finalKaProb ){
                    matchedKaId = mkProbKa[i].kaProb[j].id
                    matchedKaCode = mkProbKa[i].kaProb[j].area_code
                    matchedKaName = mkProbKa[i].kaProb[j].knowledge_area
                    matchedKaProb = mkProbKa[i].kaProb[j].finalKaProb
                }
            }
        }
        kaObj['matchedKa']['matchedKaId'] = matchedKaId
        kaObj['matchedKa']['matchedKaCode'] = matchedKaCode
        kaObj['matchedKa']['matchedKaName'] = matchedKaName
        kaObj['matchedKa']['matchedKaProb'] = matchedKaProb
        matchedKaMk.push(kaObj)
    }
}

var matchedKaMkSorted = matchedKaMk.sort((a,b) => (a.matchedKa.matchedKaNamed > b.matchedKa.matchedKaNamed) ? 1 : ((b.matchedKa.matchedKaNamed > a.matchedKa.matchedKaNamed) ? -1 : 0)); 

generatePercentageNb()
matchingKaMk()
export function matchingResultNb(){
    return { mkProbKa , matchedKaMk, matchedKaMkSorted }
}